.Board {
  margin: 0 auto;
  box-shadow: 1px 1px 70px royalblue;
}
.Board-title {
  display: inline;
}
@font-face {
  font-family: neon;
  src: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/707108/neon.ttf);
}

.container {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
}

.neon-orange {
  font-family: neon;
  color: #fb4264;
  font-size: 80px;
  line-height: 80px;
  margin: 0 auto;
  text-shadow: 0 0 3vw #f40a35;
}

.neon-blue {
  font-family: neon;
  color: #426dfb;
  font-size: 130px;
  text-shadow: 0 0 3vw #2356ff;
}
button {
  background-color: rgb(105, 216, 216);
  padding: 20px;
  margin: 90px;
  border-radius: 5px;
  font-size: 2rem;
  border: none;
}
