code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(24, 9, 18);

  padding-top: 50px;
  color: white;
  text-align: center;
}

.Cell {
  height: 100px;
  width: 100px;
  border-radius: 1px;
  background-color: rgba(126, 125, 125, 0.753);
  transition: 250ms ease-in-out;
}
@media (min-width: 400px) and (max-width: 500px) {
  .Cell {
    height: 85px;
  }
}
@media (max-width: 400px) {
  .Cell {
    height: 75px;
  }
}

.Cell-lit {
  /* background-color: #2a54ff;
  box-shadow: 1px 1px 20px #6f91ff62; */

  background-color: rgb(105, 216, 216);
  box-shadow: 1px 1px 5px rgba(105, 216, 216, 0.658);
}

.Board {
  margin: 0 auto;
  box-shadow: 1px 1px 70px royalblue;
}
.Board-title {
  display: inline;
}
@font-face {
  font-family: neon;
  src: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/707108/neon.ttf);
}

.container {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
}

.neon-orange {
  font-family: neon;
  color: #fb4264;
  font-size: 80px;
  line-height: 80px;
  margin: 0 auto;
  text-shadow: 0 0 3vw #f40a35;
}

.neon-blue {
  font-family: neon;
  color: #426dfb;
  font-size: 130px;
  text-shadow: 0 0 3vw #2356ff;
}
button {
  background-color: rgb(105, 216, 216);
  padding: 20px;
  margin: 90px;
  border-radius: 5px;
  font-size: 2rem;
  border: none;
}
